import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, MunicipalityApi } from '../../../../shared/loopback-sdk';
import * as moment from 'moment'; //for date manupalation
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-municipality.component.html'
})
export class EditMunicipalityComponent {
	@ViewChild('name') name: NgModel;
	@ViewChild('priority') priority: NgModel;
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	municipalityName: any;
	cityName:any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	municipalityStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	projectCount : number = 0;
	municipalityCount : number = 0;
	currentTime:string;
	editMunicipality={
		"id":0,
		"name":"",
		"city_name":"",
		"city_id":0,
		//"created_at":"",
		//"updated_at":"",
		"status": 0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService, private municipality:MunicipalityApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"id":this.id
		}
		var date = new Date();
		this.currentTime = moment().format("YYYY-MM-DD H:mm:s");
		console.log("time ",this.currentTime);
		this.municipality.getMunicipality(dataArrObj).subscribe(data=>{
			this.loading = false;
			if(data){
				let result = data.result[0];
				this.municipalityName = result.name;
				this.municipalityStatus = (result.status)?true:false;
				this.projectCount = result.usedCount;
				this.cityName = result.city_name;
				console.log("projectCount ",this.projectCount);
				this.editMunicipality={
					"id" : this.id,
					"name": result.name,
					"city_name":result.city_name,
					"city_id":result.city_id,
					"status": result.status
					//"created_at":result.created_at,
					//"updated_at":result.updated_at,
				}

			}
		},
		err=>{
			console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["municipality"]);
	}

	async submit(provinceForm) {
		this.submitted = true;
		if (provinceForm.valid) {
			this.loading = true;
			
			this.editMunicipality.id = this.id;
			this.editMunicipality.name = this.capital_letter(this.municipalityName);
			this.editMunicipality.status = (this.municipalityStatus)?1:0;
			//this.editMunicipality.updated_at = this.currentTime.toString();


			
			this.municipality.upsertPatch(this.editMunicipality).subscribe((data)=>{
				this.loading = false;
				this.result = "success";
				this.display = true;

			},(err)=>{
				this.loading = false;
				this.result = "Something went wrong";
				this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let api="/api/municipalities/count?where=";
				let cond = { "name": dataObj.name};
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});

			}catch(err){
				reject(err);
			}
		});
		
		
	}

	capital_letter(str) 
	{
		str = str.split(" ");

		for (var i = 0, x = str.length; i < x; i++) {
			str[i] = str[i][0].toUpperCase() + str[i].substr(1);
		}

		return str.join(" ");
	}


	
}


